import React, { useState, useEffect } from "react";
import classes from "./Clock.module.scss";

export const Clock = () => {
  const [timerOn, setTimerOn] = useState(false);
  const [timeStamp, setTimeStamp] = useState(0);
  const [second, setSecond] = useState(0);
  const [minute, setMinute] = useState(0);
  const [hour, setHour] = useState(0);

  useEffect(() => {
    if (timerOn) {
      const interval = setInterval(() => {
        setSecond((second) => second + 1);
      }, 1000);
      return () => {
        clearInterval(interval);
      };
    }
  }, [timerOn]);

  useEffect(() => {
    if (second === 60) {
      setMinute((minute) => minute + 1);
      setSecond(0);
    }
    if (minute === 60) {
      setHour((hour) => hour + 1);
      setMinute(0);
    }
  }, [second, minute, hour]);

  const getTime = () => {
    return `${normalizeTime(hour)}:${normalizeTime(minute)}:${normalizeTime(
      second
    )}`;
  };

  const resetTime = () => {
    setSecond(0);
    setMinute(0);
    setHour(0);
  };

  return (
    <div className={classes["clock"]}>
      <p className={classes["clock__display"]}>{getTime()}</p>
      <div className={classes["clock__btns"]}>
        <button
          className={`${classes["clock__btn"]} ${classes["clock__btn_start"]}`}
          onClick={() => {
            if (!timerOn) {
              setTimerOn(!timerOn);
            } else {
              setTimerOn(!timerOn);
              resetTime();
            }
          }}
        >
          Start/Stop
        </button>
        <button
          className={`${classes["clock__btn"]} ${classes["clock__btn_wait"]}`}
          onClick={(e) => {
            if (e.timeStamp - timeStamp < 300) {
              setTimerOn(false);
            }
            setTimeStamp(e.timeStamp);
          }}
        >
          Wait
        </button>
        <button
          className={`${classes["clock__btn"]} ${classes["clock__btn_reset"]}`}
          onClick={resetTime}
        >
          Reset
        </button>
      </div>
    </div>
  );
};

function normalizeTime(time) {
  return time < 10 ? `0${time}` : time;
}
